.news {
	display: flex;
	padding: 72px 20px;
	flex-direction: column;
}
.news h1 {
	text-align: center;
	padding-bottom: 40px;
	color: #64008a;
	font-weight: 200;
	font-size: 30pt;
}
.new-item {
	display: flex;
	width: 100%;
	max-width: 1000px;
	flex-direction: row;
	margin: 10px auto;
}
.new-item a {
	box-shadow: 5px 5px 10px #848484;
	padding: 20px;
	width: 100%;
	border-top-left-radius: 25px;
	border-bottom-right-radius: 25px;
	text-decoration: none;
}
.new-item a h2 {
	color: rgba(0, 0, 0, .6);
	padding-bottom: 10px;
}
.new-item a strong {
	color: rgba(0, 0, 0, .4);
	padding: 10px 0px;
}

@media only screen and (max-width: 600px) {
	.block-content {
		flex-wrap: wrap;
	}
	.left-block {
		width: 50%;
	}
}
